@import url(https://fonts.googleapis.com/css?family=Press+Start+2P&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#root {
  display: -webkit-flex;
  display: flex;
  width: 100%;
  -webkit-flex-direction: column;
          flex-direction: column;
  padding-bottom: 30px;
  -webkit-align-items: center;
          align-items: center;  
  
  background-color: salmon;
}

.card-list {
  display: grid;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 30px;
  padding-top: 100px;
  width: 85vw;

  grid-template-columns: 1fr;
  grid-gap: 20px;
}

@media (min-width: 709px) and (max-width: 1044px) {
  .card-list {
    grid-template-columns: 1fr 1fr;
  }
}

@media (min-width: 1045px) and (max-width: 1400px) {
  .card-list {
    grid-template-columns: 1fr 1fr 1fr;
  }
}

@media (min-width: 1401px) {
  .card-list {
    grid-template-columns: 1fr 1fr 1fr 1fr;
  }
}
.card {
  display: block;
  margin: auto;
  box-shadow: 0 0 20px 2px black;
  background-color: white;
  transition: 0.5s ease-in;
}

.card:hover {
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}

.art {
  width: 300px;
}

.label {
  font-family: 'Press Start 2P', cursive;
  font-size: 25px;
  text-shadow: 0 0 5px firebrick, 0 0 10px yellow;
  text-align: center;
  padding: 10px;
}

.top-bar {
  position: fixed;
  z-index: 1;

  display: -webkit-flex;

  display: flex;
  width: 100%;
  height: 80px;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;

  background-color: rgba(0, 0, 0, 0.24);
}

@media (max-width: 720px) {
  .top-bar button {
    font-size: 10px;
  }
}

.search-box {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-basis: 50%;
          flex-basis: 50%;
  height: 27px;
  padding-left: 5px;

  font-family: 'Press Start 2P', cursive;
  font-size: 20px;
  color: rgba(218, 133, 7, 0.89);
}

.search-box::-webkit-input-placeholder {
  text-align: center;
}

.search-box:-ms-input-placeholder {
  text-align: center;
}

.search-box::-ms-input-placeholder {
  text-align: center;
}

.search-box::placeholder {
  text-align: center;
}

@media (max-width: 720px) {
  .search-box {
    font-size: 13px;
  }
}
.button {
  display: -webkit-flex;
  display: flex;
  width: 20%;
  height: 35px;
  -webkit-justify-content: center;
          justify-content: center;

  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande',
    'Lucida Sans', Arial, sans-serif;
  font-size: 20px;
  font-weight: bold;
  text-transform: uppercase;
  letter-spacing: 3px;
  color: white;

  border: solid rgb(71, 71, 67) 2px;
  background-color: rgba(218, 133, 7, 0.89);
  transition: ease-in 0.5s;
}

.button:hover {
  -webkit-filter: invert(100%);
          filter: invert(100%);
}

.button:active {
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}

